$font_path: '../../fonts/';

@import '../../scss/helpers/helpers-all';
@import '../../scss/reset';

// ---------------------------------------------------------------------------------fonts#
@include font_face('Inter-Regular', 'Inter', $font_path, 400);
@include font_face('Inter-Bold', 'Inter', $font_path, 700);
// ---------------------------------------------------------------------------------fonts##

// // ---------------------------------------------------------------------------------base#

//vars
$point_1: 1200px;
$point_2: 1024px;
$point_3: 1023px;
$point_4: 768px;
$point_5: 640px;
$point_6: 428px;
$point_7: 375px;
$point_8: 320px;
$point_9: 1400px;
$point_10: 1600px;
////////
:root {
	--full-screen: calc(var(--vh, 1vh) * 100);

	--content-width: 135.6em;

	--font_1: 'Inter', sans-serif;
	// --font-size-html: calc(100vw / 1920 * 10);
	--font-size-html: 10px;
	--font-size-body: 1em;
	--black_1: #000;
	--white_1: #fff;
	--color-text-base: var(--black_1);
	--body-background: var(--white_1);
	--accent-color: #4aa3df;
	--input-placeholder-color: var(--color-text-base);

	@include bp($point_2, min) {
		--gl-indent: 4em;
	}

	@include bp($point_3) {
		--gl-indent: 2em;
	}
}

html {
	height: 100%;

	font-family: var(--font_1);
	font-weight: 400;
	line-height: 1.33;
	font-size: var(--font-size-html);
}

body {
	position: relative;

	height: 100%;

	font-size: var(--font-size-body);
	color: var(--color-text-base);

	-webkit-text-size-adjust: none;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;

	background: var(--body-background);

	&.body--open_menu_state {
		overflow: hidden;
	}
}

.wrapper {
	position: relative;

	width: 100%;
	display: flex;
	flex-direction: column;
	min-height: 100%;
	height: auto !important;

	overflow-x: hidden;
}

.base {
	width: 100%;
	position: relative;

	flex: 1 0 auto;
}

.section {
	position: relative;

	width: 100%;

	font-size: 1rem;
}

.section_in {
	max-width: var(--content-width);
	width: 100%;
	margin: 0 auto;
}

a {
	text-decoration: none;
	color: var(--accent-color);

	&:hover,
	&:focus {
		text-decoration: underline;
	}
}

.icon {
	fill: currentColor;
	display: block;

	&.icon--size_mod {
		height: 100%;
		width: 100%;
	}
}
// ---------------------------------------------------------------------------------base##
// ---------------------------------------------------------------------------------styles for components#
// ---------------------------------------------------------------------------------styles for components##

// HEADER
.header {
	position: fixed;
	top: 0;
	left: 0;

	height: var(--height-header);
	width: 100%;

	font-size: 1em;

	will-change: transform;
	z-index: 100;

	@include bp($point_2, min) {
		padding: 3.2em var(--gl-indent) 0;
	}

	@include bp($point_3) {
		padding: 1.5em var(--gl-indent);
	}
}

.header_in {
	@include flex_block(row, nowrap, flex-start, center);
	position: relative;
	max-width: var(--content-width);
	width: 100%;
	margin: 0 auto;
}

.header__logo {
	display: block;
	width: 3.5em;
	height: 3.5em;

	@include bp($point_2, min) {
		margin-right: auto;
	}

	@include bp($point_3) {
		margin-right: auto;
	}
}

.header__logo_img {
	@include contain_img;
}

.header_nav {
	@include bp($point_2, min) {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
	}
	@include bp($point_3) {
		@include flex_block(column, nowrap, flex-start, flex-start);
		position: fixed;
		top: 0;
		right: -100%;
		width: 100%;
		height: var(--full-screen);
		padding: 5em var(--gl-indent);
		background-color: var(--white_1);
		overflow-x: hidden;
		overflow-y: auto;
		transform: translateX(0);
		transition: transform 0.3s ease-in-out;

		.body--open_menu_state & {
			transform: translateX(-100%);
		}
	}
}

.header_nav__list {
	@include bp($point_2, min) {
		@include flex_block(row, nowrap, flex-start, center);
	}

	@include bp($point_3) {
		@include flex_block(column, nowrap, flex-start, center);
		margin: auto;
	}
}

.header_nav__item {
	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-right: 5.1vw;
		}

		@include bp($point_3) {
			margin-bottom: 3em;
		}
	}
}

.header_nav__link {
	display: block;
	font-weight: 700;
	font-size: 1.6em;
	line-height: 1.9em;
	color: var(--color-text-base);
	text-transform: uppercase;
	transition: color 0.3s ease-in-out;

	&:hover,
	&:focus {
		color: var(--accent-color);
		text-decoration: none;
	}
}

.header_btns {
	@include flex_block(row, nowrap, flex-start, center);

	@include bp($point_3) {
		&:not(:last-child) {
			margin-right: 3em;
		}
	}
}

.header_btns__item {
	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-right: 1.4vw;
		}

		@include bp($point_3) {
			margin-right: 1.2em;
		}
	}
}

.header_btns__button {
	display: block;
	width: 2.9em;
	height: 2.9em;
	color: var(--color-text-base);
	cursor: pointer;
	transition: color 0.3s ease-in-out;

	&:hover,
	&:focus {
		color: var(--accent-color);
	}
}

.header__burger {
	position: relative;

	width: 4em;
	height: 3em;

	background-color: transparent;

	cursor: pointer;
	z-index: 101;

	@include bp($point_2, min) {
		display: none;
	}

	@include bp($point_3) {
		display: block;
	}

	&::before,
	&::after {
		content: '';
		position: absolute;
		top: 50%;
		left: 50%;

		width: 100%;
		height: 0.3em;

		background-color: var(--color-text-base);
		transition: transform 0.3s ease-in-out;
	}

	&::before {
		transform: translate(-50%, calc(-50% - 1em));

		.body--open_menu_state & {
			transform: translate(-50%, -50%) rotate(45deg);
		}
	}

	&::after {
		transform: translate(-50%, calc(-50% + 1em));
		.body--open_menu_state & {
			transform: translate(-50%, -50%) rotate(-45deg);
		}
	}
}

.header__burger_decor {
	position: absolute;
	top: 50%;
	left: 50%;

	width: 100%;
	height: 0.3em;

	background-color: var(--color-text-base);

	transform: translate(-50%, -50%);

	transition: opacity 0.3s ease;

	.body--open_menu_state & {
		opacity: 0;
	}
}

////////
///
///
///
///
.sneaker_slider_in {
	max-width: 100%;

	@include bp($point_2, min) {
		font-size: calc(100vw / 1440 * 10);
	}
	
	@include bp($point_3) {
		font-size: calc(100vmin / 1440 * 10);
	}
}

.sneaker_carousel {
	@include flex_block(row, nowrap, flex-start, stretch);
	height: var(--full-screen);
	overflow-y: auto;
}

.sneaker_carousel__pagination {
	@include flex_block(row, nowrap, flex-start, center);
	position: absolute;
	bottom: 3em;
	left: 50%;

	width: auto;
	height: 0.8em;

	transform: translateX(-50%);
	font-size: 10px;

	z-index: 3;
	@include bp($point_2, min) {
		display: none;
	}

	.swiper-pagination-bullet {
		display: block;

		width: 1.5em;
		height: 1.5em;

		background-color: var(--black_1);
		border-radius: 2em;
		opacity: 100%;

		transition: width 0.3s ease-in-out, border-radius 0.3s ease-in-out;

		&:not(:last-child) {
			margin-right: 1.2em;
		}
	}

	.swiper-pagination-bullet-active {
		width: 3.2em;
		border-radius: 0.8em;
	}
}

.sneaker_carousel__item {
	@include flex_block(column, nowrap, center, center);
	position: relative;
	padding: 5em 1em;
	overflow: hidden;
	cursor: pointer;

	transition: flex-grow 0.4s ease-in-out;
	will-change: flex-grow;

	
	@include bp($point_2, min) {
		flex: 1;
	}
	
	@include bp($point_3) {
		width: 100%;
	}

	&:nth-child(3n + 1) {
		background: radial-gradient(125.54% 125.54% at 50% 0.93%, #40a3ff 0%, rgba(0, 81, 155, 0) 100%);
	}

	&:nth-child(3n + 2) {
		background: radial-gradient(133.45% 133.45% at 50% 7.47%, #cfff23 0%, rgba(121, 154, 0, 0) 100%);
	}
	&:nth-child(3n + 3) {
		background: #f3dfc5;
	}

	&.sneaker_carousel__item--active_state {
		@include bp($point_2, min) {
			flex: 4.5;
		}
	}
}

.sneaker_carousel__item_link {
	display: block;
	width: 95%;
	height: auto;
	pointer-events: none;
	max-width: 95vmin;

	@include bp($point_2, min) {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-60%, -55%) rotate(-31deg);
		transition: transform 0.4s ease-in-out;
		will-change: transform;
		z-index: 2;

		.sneaker_carousel__item--active_state & {
			transform: translate(-57%, -53%) rotate(-13deg) scale(0.76);
			pointer-events: initial;
		}
	}

	@include bp($point_3) {
		margin-bottom: 4em;
		height: 82em;
	}
}

.sneaker_carousel__item_img_w {
	display: block;
	height: 100%;
	width: 100%;
}

.sneaker_carousel__item_img {
	@include contain_img;
}

.sneaker_carousel__item_text {
	color: var(--white_1);
	text-transform: uppercase;

	@include bp($point_2, min) {
		font-weight: 400;
		font-size: 3em;
		transform: scale(3.8);
		transition: transform 0.4s ease-in-out, font-weight 0.4s ease-in-out;
		will-change: transform;

		
		&.sneaker_carousel__item_text--size_mod {
			transform: scale(2.6);
		}

		.sneaker_carousel__item--collapsed_state &.sneaker_carousel__item_text--size_mod {
			transform: scale(1.1);
		}
	}


	.sneaker_carousel__item--collapsed_state & {
		@include bp($point_2, min) {
			transform: scale(1.8);
		}
	}

	.sneaker_carousel__item--active_state & {
		@include bp($point_2, min) {
			transform: scale(4.5) translateY(8.5vh);
			font-weight: 700;
		}
	}

	@include bp($point_3) {
		font-weight: 700;
		font-size: 14em;
	}
}

.swiper-slide {
	@include bp($point_2, min) {
		flex-shrink: 1;
	}
}
